import 'script-loader!../../lib/zlibjs/inflate.min.js'; // 로더를 위해 필요한 외부 라이브러리.
import 'script-loader!../../lib/threejs/three.min.js';
import 'script-loader!../../lib/threejs/CSS3DRenderer.min.js';
import 'script-loader!../../lib/threejs/LoaderSupport.min.js';
import 'script-loader!../../lib/threejs/MTLLoader.min.js';
import 'script-loader!../../lib/threejs/OBJLoader2.min.js';
import 'script-loader!../../lib/threejs/STLLoader.min.js';
import 'script-loader!../../lib/threejs/FBXLoader.min.js';
import 'script-loader!../../lib/arjs/ar.min.js';

import Property from './property';
import Utils from './utils';
import Options from './options';
import Asset from './asset';
import AssetLoader from './asset-loader';
import AssetManager from './asset-manager';
import AssetCssRenderer from './asset-css-renderer';
import ArScreenshot from './ar-screenshot';
import CssUnitV from './css-unit-v';
import Raycaster from './raycaster';
import TrensformAverage from './trensform-average';

export default class NemoAr {
    constructor(obj) {
        const isSupport = !(!window.navigator || !window.navigator.mediaDevices || !window.navigator.mediaDevices.getUserMedia);

        this.options = new Options(obj);

        if (isSupport) {
            try {
                // 외부 라이브러리에서 수정되어야 하는 부분 재정의.
                this.__overriding();

                this.fieldEl = Utils.createDivElement();
                this.scene = new THREE.Scene();
                this.light = new THREE.DirectionalLight();
                this.camera = new THREE.Camera();
                this.renderer = new THREE.WebGLRenderer({antialias: true, alpha: true, preserveDrawingBuffer: true});
                this.clock = new THREE.Clock();

                this.arToolkitSource = null;
                this.arToolkitContext = null;
                this.assetLoader = new AssetLoader(this.options.baseUrl);
                this.assetCssRenderer = new AssetCssRenderer(this.renderer, this.camera, CssUnitV);
                this.assetManager = new AssetManager(this.scene, this.assetLoader, this.assetCssRenderer);
                this.arScreenshot = new ArScreenshot(this);
                this.raycaster = new Raycaster(this.renderer, this.camera);

                this.contentSizeGapW = 0;
                this.contentSizeGapH = 0;

                this.multiMarkerControls = null;
                this.smoothedControls = null;

                this.isRun = false;
                /**
                 * 에셋이 화면에 보여지고 있는지 여부를 반환한다.
                 */
                this.isMarkerFound = false;
                this.__isMarkerFoundTimeout = null;

                this.__useFrontCamera = this.options.useFrontCamera;

                this.__init();

            } catch (error) {
                console.error(error);
                this.options.onError(error);
            }
        } else {
            this.options.onError(new Error('is not support!'));
        }
    }

    /**
     * 소스가 보여지고 있는지 여부를 반환한다.
     * @returns {boolean} 보여지고 있으면 true.
     */
    get isSourceView() {
        const me = this;
        const videoEl = me.arToolkitSource.domElement;

        return videoEl.style.opacity != '0';
    }

    /**
     * 화면에 보여줄 에셋을 등록한다.
     * @param {Asset || object} obj 에셋정보가 들어있는 객체.
     * @param {function(itemCount, itemTotal, assetItem, asset)} onProgress 에셋을 구성하는 아이템을 받을 때마다 실행되는 함수.
     */
    import(obj, onProgress = function () {}) {
        const me = this;

        let asset = new Asset(obj);

        asset.onShow = me.options.onShowAsset;
        asset.onHide = me.options.onHideAsset;

        // 컨트롤러에는 비어있는 3D객체를 넣는다.
        const markerControls = new THREEx.ArMarkerControls(me.arToolkitContext, new THREE.Object3D(), {
            type: asset.markerType,
            patternUrl: me.options.baseUrl + '/' + asset.markerPatternUrl
        });

        markerControls.addEventListener('markerFound', function () {
            clearTimeout(me.__isMarkerFoundTimeout);

            me.isMarkerFound = true;

            me.__isMarkerFoundTimeout = setTimeout(function () {
                me.isMarkerFound = false;
            }, 100);
        });

        asset.controllerId = markerControls.id;

        return me.assetLoader.load(asset, onProgress).then(function (currentAsset) {
            me.assetManager.add(currentAsset);

            return me.assetManager.caching(currentAsset.controllerId, me.options.loadCachingCount);

        }).then(function () {
            return Promise.resolve(asset);
        });
    }

    /**
     * 현재 사용되고 있는 마커 이미지 url이 들어가 있는 배열을 반환한다.
     */
    getMarkerUrlArr() {
        const me = this;

        return me.assetManager.getMarkerUrlArr();
    }

    /**
     * 이름을 입력받아 해당 이름을 가진 아이템 정보를 반환한다. 중복시 처음 찾은 정보만 반환한다.
     * @param {string} name assetItem에 지정된 이름.
     */
    getItemByName(name) {
        const me = this;

        return me.assetManager.getItemByName(name);
    }

    /**
     * 모든 아이템이 들어가 있는 배열을 반환한다.
     */
    getItemArray() {
        const me = this;

        return me.assetManager.getItemArray();
    }

    /**
     * 화면의 사이즈를 재설정 한다.
     * @param {number} width 화면의 넓이.
     * @param {number} height 화면의 높이.
     */
    resize(width = Property.DEFAULT_WIDTH, height = Property.DEFAULT_HEIGHT) {
        const me = this;

        me.options.width = width;
        me.options.height = height;

        me.__resize();
    }

    /**
     * 뷰어 화면을 찍는 promise를 반환한다.
     */
    screenshot() {
        const me = this;

        return new Promise(function (resolve, reject) {
            try {
                me.assetCssRenderer.hideObject3D(false);

                setTimeout(function () {
                    const canvas = me.arScreenshot.screenshot();

                    me.assetCssRenderer.showObject3D(false);

                    resolve(canvas);
                }, 100);

            } catch (error) {
                reject(error);
            }
        });
    }

    /**
     * 에셋을 구성하는 아이템 팝업이 열려 있으면 닫는다.
     */
    closeItemPopup() {
        const me = this;

        if (me.assetCssRenderer.closeItemPopup) {
            me.assetCssRenderer.closeItemPopup();
        }
    }

    /**
     * 영상처리를 시작한다.
     */
    start() {
        const me = this;

        let animationTimeStamp = 0;

        me.isRun = true;
        me.arToolkitSource.domElement.play();
        me.showAssets();

        (function animate (t) {
            let time = t - animationTimeStamp;

            if (me.isRun) {
                if (time >= Property.DEFAULT_FPS_TIME) {
                    animationTimeStamp = t;

                    // 마커 추적.
                    me.arToolkitContext.update(me.arToolkitSource.domElement);

                    // 3d갱신
                    me.assetManager.animation(me.clock.getDelta());
                    me.scene.visible = me.camera.visible;
                    me.renderer.render(me.scene, me.camera);

                    // 2d갱신
                    me.assetCssRenderer.render();
                }

                requestAnimationFrame(animate);
            }
        })(0);
    }

    /**
     * 영상처리를 멈춘다.
     */
    stop () {
        const me = this;

        me.isRun = false;
        me.arToolkitSource.domElement.pause();
        me.hideAssets();
    }

    /**
     * 사용 카메라를 전환한다.
     */
    changeCamera() {
        const me = this;

        if (me.arToolkitSource) {
            const videoEl = me.arToolkitSource.domElement;

            videoEl.srcObject.getTracks().forEach(function (track) {
                track.stop();
            });

            me.fieldEl.removeChild(videoEl);
            me.fieldEl.removeChild(me.assetCssRenderer.domElement);
            me.fieldEl.removeChild(me.renderer.domElement);
        }

        me.__useFrontCamera = (!me.__useFrontCamera);

        me.__initSource();
    }

    /**
     * 소스 비디오를 보여준다.
     */
    showSource() {
        const me = this;
        const videoEl = me.arToolkitSource.domElement;

        videoEl.style.opacity = '1';
    }

    /**
     * 소스 비디오를 가린다.
     */
    hideSource() {
        const me = this;
        const videoEl = me.arToolkitSource.domElement;
        // video가 재생되는 도중에는 모바일 화면 자동꺼짐이 작동하지 않는다.
        // display, visibility 속성을 사용해 숨기는 경우 video가 재생되고 있어도 IOS환경에서 화면 자동꺼짐이 작동한다.
        videoEl.style.opacity = '0';
    }

    /**
     * 화면에 에셋을 보여준다.
     */
    showAssets() {
        const me = this;

        me.renderer.domElement.style.visibility = '';
        me.assetCssRenderer.domElement.style.visibility = '';
    }

    /**
     * 화면에 에셋을 가린다.
     */
    hideAssets() {
        const me = this;

        me.renderer.domElement.style.visibility = 'hidden';
        me.assetCssRenderer.domElement.style.visibility = 'hidden';
    }

    __initSource() {
        const me = this;

        me.arToolkitSource = new THREEx.ArToolkitSource({
            sourceType: me.options.sourceType,
            sourceUrl: me.options.sourceUrl,
            sourceWidth: Property.SOURCE_WIDTH,
            sourceHeight: Property.SOURCE_HEIGHT,
            displayWidth: Property.DISPLAY_WIDTH,
            displayHeight: Property.DISPLAY_HEIGHT
        });

        me.arToolkitSource.init(function () {
            const videoEl = me.arToolkitSource.domElement;

            me.renderer.domElement.style.position = 'absolute';

            me.light.position.set(0, 0, 1);

            me.fieldEl.appendChild(videoEl);
            me.fieldEl.appendChild(me.assetCssRenderer.domElement);
            me.fieldEl.appendChild(me.renderer.domElement);
            me.fieldEl.style.overflow = 'hidden';
            me.fieldEl.style.position = 'relative';
            me.fieldEl.id = Property.ELEMENT_FIELD_ID;
            me.fieldEl.classList.add(Property.ELEMENT_FIELD_CLASS_NAME);

            me.options.rootElement.appendChild(me.fieldEl);

            // ar.js 내부에 있는 domElement를 기본 위치에서 변경하면 video재생이 멈추기 때문에 다시 플레이 한다.
            videoEl.play();

            me.__resize();

            me.start();

            me.options.onInit();

        }, me.options.onError);
    }

    __initContext() {
        const me = this;

        me.arToolkitContext = new THREEx.ArToolkitContext({
            cameraParametersUrl: THREEx.ArToolkitContext.baseURL + './' + Property.CAMERA_PARA_NAME,
            detectionMode: Property.DETECTION_MODE,
            matrixCodeType: Property.MATRIX_CODE_TYPE,
            maxDetectionRate: Property.MAX_DETECTION_RATE,
            patternRatio: me.options.patternRatio,
            imageSmoothingEnabled: false
        });

        me.arToolkitContext.init(function () {
            me.camera.projectionMatrix.copy(me.arToolkitContext.getProjectionMatrix());

            me.arToolkitContext.addEventListener('sourceProcessed', function () {
                // 프로세스가 끝날 때 마다 화면의 객체 수거.
                me.assetManager.recover();
            });
        });
    }

    __resize() {
        const me = this;

        const fieldEl = me.fieldEl;
        const fieldW = me.options.width;
        const fieldH = me.options.height;

        const videoEl = me.arToolkitSource.domElement;
        const sourceW = videoEl.videoWidth;
        const sourceH = videoEl.videoHeight;

        let videoW = Property.DISPLAY_WIDTH;
        let videoH = Property.DISPLAY_HEIGHT;

        fieldEl.style.width = fieldW + 'px';
        fieldEl.style.height = fieldH + 'px';

        // video영역에 그려지는 소스의 크기를 구한다.
        // 소스의 방향과 상관 없이 video영역의 높이를 기준으로 한다.
        const drawSourceW = (videoH / sourceH) * sourceW;
        const drawSourceH = videoH;

        // 확대 비율이 큰 방향을 구한다.
        // 가로 방향으로 늘리는 비율.
        let videoAspect = fieldW / drawSourceW;

        // 세로 방향으로 늘리는 비율이 더 크다면 세로 방향 비율을 사용한다.
        if (videoAspect < (fieldH / drawSourceH)) {
            videoAspect = fieldH / drawSourceH;
        }

        // 소스가 커져야 할 비율만큼 video영역을 키운다.
        videoW = videoW * videoAspect;
        videoH = videoH * videoAspect;

        const left = (fieldW - videoW) / 2;
        const top = (fieldH - videoH) / 2;

        // 필드 내부에 표시될 레이어들 간에 간격을 보정하기 위한 값이 있다.(보정값 설명 참고)
        // 비디오와 캔버스의 크기를 보정값을 더한 크기로 만든다.
        // css 랜더러의 크기를 변경하고 가운데 정렬을 할때 해당 보정값 만큼 추가로 이동시킨다. ( me.assetCssRenderer.resize() 내부 )
        // 필드의 스크롤을 이동하여 화면에 잘리는 요소가 없는 위치로 이동한다.
        me.contentSizeGapW = fieldW * Property.CSSRENDERER_POSITION_GAP_X;
        me.contentSizeGapH = fieldH * Property.CSSRENDERER_POSITION_GAP_Y;

        videoW += (me.contentSizeGapW * 2);
        videoH += (me.contentSizeGapH * 2);

        videoEl.style.left = left + 'px';
        videoEl.style.top = top + 'px';
        videoEl.style.width = videoW + 'px';
        videoEl.style.height = videoH + 'px';

        const canvasEl = me.renderer.domElement;

        canvasEl.style.left = left + 'px';
        canvasEl.style.top = top + 'px';
        me.renderer.setSize(videoW, videoH);

        me.assetCssRenderer.resize();

        me.fieldEl.scrollLeft = me.contentSizeGapW;
        me.fieldEl.scrollTop = me.contentSizeGapH;
    }

    __intersect(evt) {
        const me = this;

        const intersectChild = me.raycaster.intersect(this.scene.children, evt.offsetX, evt.offsetY);

        let group = null;

        if (intersectChild) {
            intersectChild.traverseAncestors(function (object3D) {
                if (object3D.parent && object3D.parent.name === Property.ITEM_OBJECT_FIELD_NAME) {
                    group = object3D;
                }
            });
        }

        return me.assetManager.getItemByObject3D(group);
    }

    /**
     * AR 초기화.
     */
    __init() {
        const me = this;

        window.THREEx.ArToolkitContext.baseURL = me.options.arToolkitBaseUrl + '/';

        me.scene.visible = false;
        me.scene.add(me.light);
        me.scene.add(me.camera);

        // AR 화면을 클릭했을 때.
        me.renderer.domElement.addEventListener('mousedown', function (evt) {
            const intersectedItem = me.__intersect(evt);

            // 유튜브.
            if (evt.which === 1 && intersectedItem && intersectedItem.type === Property.ITEM_TYPE_YOUTUBE) {
                me.stop();
                me.showAssets(); // stop()를 호출하면 에셋 레이어가 전부 가려지기 때문에 에셋을 다시 띄운다.

                me.assetCssRenderer.itemPopup(intersectedItem, function () {
                    me.options.onPopup();

                }, function () {
                    me.start();
                });
            }

            if (evt.which === 1 && intersectedItem) {
                me.options.onClick(intersectedItem);
            }
        });

        me.__initSource();

        me.__initContext();
    }

    /**
     * 외부 라이브러리에서 내부적으로 변경이 필요한 기능을 재정의 한다. 외부 라이브러리에 의존성을 가진다.
    */
    __overriding() {
        const me = this;

        // 오브젝트의 위치를 갱신하는 함수를 오버라이딩 한다.
        if (THREEx && THREEx.ArMarkerControls.prototype.updateWithModelViewMatrix) {
            console.log('Overriding THREEx.ArMarkerControls.prototype.updateWithModelViewMatrix');

            THREEx.ArMarkerControls.prototype.updateWithModelViewMatrix = function(modelViewMatrix){
                const trensformAverageSize = me.options.trensformAverageSize;
                const asset = me.assetManager.borrow(this.id);

                if (asset) {
                    const markerObject3D = asset.object3D;
                    // apply context._axisTransformMatrix - change artoolkit axis to match usual webgl one
                    const tmpMatrix = new THREE.Matrix4().copy(this.context._artoolkitProjectionAxisTransformMatrix);

                    tmpMatrix.multiply(modelViewMatrix);
                    modelViewMatrix.copy(tmpMatrix);

                    // change axis orientation on marker - artoolkit say Z is normal to the marker - ar.js say Y is normal to the marker
                    const markerAxisTransformMatrix = new THREE.Matrix4().makeRotationX(Math.PI/2);
                    modelViewMatrix.multiply(markerAxisTransformMatrix);

                    // change markerObject3D.matrix based on parameters.changeMatrixMode
                    markerObject3D.matrix.copy(modelViewMatrix);

                    // decompose - the matrix into .position, .quaternion, .scale
                    markerObject3D.matrix.decompose(markerObject3D.position, markerObject3D.quaternion, markerObject3D.scale);

                    // 위치 평균값 처리
                    if (!asset.trensformAverage) {
                        asset.trensformAverage = new TrensformAverage(trensformAverageSize);
                    }

                    asset.trensformAverage.add(markerObject3D);

                    const transformInfo = asset.trensformAverage.getAverage();

                    markerObject3D.position.copy(transformInfo.position);
                    markerObject3D.scale.copy(transformInfo.scale);
                    markerObject3D.rotation.copy(transformInfo.rotation);
                    // end - 위치 평균값 처리

                    me.assetCssRenderer.update(asset);

                    // dispatchEvent
                    this.dispatchEvent({type: 'markerFound'});
                }
            };
        } else {
            const str = [
                'Overriding THREEx.ArMarkerControls.prototype.updateWithModelViewMatrix를 찾을 수 없습니다.',
                '해당 함수를 재정의 해야 오류가 생기지 않습니다.'
            ].join('\n');

            console.error(str);
        }

        // 카메라 리소스를 초기화 하는 부분.
        // 초기화 할 때 방향을 지정하기 위해 수정.
        if (THREEx && THREEx.ArToolkitSource.prototype._initSourceWebcam) {
            console.log('Overriding THREEx.ArToolkitSource.prototype._initSourceWebcam');

            THREEx.ArToolkitSource.prototype._initSourceWebcam = function (onReady, onError) {
                const _this = this;

                // init default value
                onError = onError || function(error){
                    alert('Webcam Error\nName: '+error.name + '\nMessage: '+error.message);
                };

                const domElement = document.createElement('video');
                domElement.setAttribute('autoplay', 'autoplay');
                domElement.setAttribute('muted', 'muted');
                domElement.setAttribute('playsinline', 'playsinline');
                domElement.style.width = this.parameters.displayWidth+'px';
                domElement.style.height = this.parameters.displayHeight+'px';

                // check API is available
                if (navigator.mediaDevices === undefined || navigator.mediaDevices.enumerateDevices === undefined || navigator.mediaDevices.getUserMedia === undefined){
                    let fctName;

                    if(navigator.mediaDevices === undefined) {
                        fctName = 'navigator.mediaDevices';

                    } else if(navigator.mediaDevices.enumerateDevices === undefined) {
                        fctName = 'navigator.mediaDevices.enumerateDevices';

                    } else if(navigator.mediaDevices.getUserMedia === undefined) {
                        fctName = 'navigator.mediaDevices.getUserMedia';

                    } else {
                        console.assert(false);
                    }

                    onError({
                        name: '',
                        message: 'WebRTC issue-! '+fctName+' not present in your browser'
                    });

                    return null;
                }

                // get available devices
                const userMediaConstraints = {
                    audio: false,
                    video: {
                        facingMode: me.__useFrontCamera ? 'user' : 'environment',
                        width: {
                            ideal: _this.parameters.sourceWidth,
                        },
                        height: {
                            ideal: _this.parameters.sourceHeight,
                        }
                    }
                };

                // get a device which satisfy the constraints
                navigator.mediaDevices.getUserMedia(userMediaConstraints).then(function success(stream) {
                    // set the .src of the domElement
                    domElement.srcObject = stream;

                    // TODO listen to loadedmetadata instead
                    // wait until the video stream is ready
                    let interval = setInterval(function() {
                        if (!domElement.videoWidth) {
                            return;
                        }

                        onReady();

                        clearInterval(interval);

                    }, 1000/50);

                }).catch(function(error) {
                    onError({
                        name: error.name,
                        message: error.message
                    });
                });

                return domElement;
            };

        } else {
            const str = [
                'THREEx.ArToolkitSource.prototype._initSourceWebcam를 찾을 수 없습니다.',
                '해당 함수를 재정의 해야 오류가 생기지 않습니다.'
            ].join('\n');

            console.error(str);
        }

        if (THREEx && THREEx.ArToolkitSource.prototype._initSourceVideo) {
            console.log('Overriding THREEx.ArToolkitSource.prototype._initSourceVideo');

            THREEx.ArToolkitSource.prototype._initSourceVideo = function(onReady) {
                // TODO make it static
                let domElement = document.createElement('video');
                domElement.src = this.parameters.sourceUrl;

                domElement.style.objectFit = 'initial';

                domElement.setAttribute('autoplay', 'autoplay');
                domElement.setAttribute('muted', 'muted');
                domElement.setAttribute('playsinline', 'playsinline');
                domElement.setAttribute('loop', 'loop');
                domElement.setAttribute('crossorigin', 'anonymous');

                domElement.width = this.parameters.sourceWidth;
                domElement.height = this.parameters.sourceHeight;
                domElement.style.width = this.parameters.displayWidth+'px';
                domElement.style.height = this.parameters.displayHeight+'px';

                // wait until the video stream is ready
                let interval = setInterval(function() {
                    if (!domElement.videoWidth) {
                        return;
                    }

                    onReady();

                    clearInterval(interval);

                }, 1000/50);

                return domElement;
            };

        } else {
            const str = [
                'THREEx.ArToolkitSource.prototype._initSourceVideo 찾을 수 없습니다.',
                '해당 함수를 재정의 해야 오류가 생기지 않습니다.'
            ].join('\n');

            console.error(str);
        }

        // 패턴 마커를 가지고 매트릭스를 계산하는 부분에서 호출되는 함수.
        // inPrevious 값에 의해 중복된 마커 이미지 계산에 오류가 생기기 때문에 false로 고정하여 반환한다.
        if (ARController && ARController.prototype.trackPatternMarkerId) {
            console.log('Overriding ARController.prototype.trackPatternMarkerId');

            ARController.prototype.trackPatternMarkerId = function(id, markerWidth) {
                let obj = this.patternMarkers[id];

                if (!obj) {
                    this.patternMarkers[id] = obj = {
                        inPrevious: false,
                        inCurrent: false,
                        matrix: new Float32Array(12),
                        markerWidth: markerWidth || this.defaultMarkerWidth
                    };
                }

                if (markerWidth) {
                    obj.markerWidth = markerWidth;
                }

                // 같은 마커 이미지가 화면에 표시될 수 있도록 하기위한 옵션 변경
                obj.inPrevious = false;
                obj.inCurrent = false;

                return obj;
            };
        } else {
            const str = [
                'Overriding ARController.prototype.trackPatternMarkerId를 찾을 수 없습니다.',
                '해당 함수를 재정의 해야 오류가 생기지 않습니다.'
            ].join('\n');

            console.error(str);
        }
    }
}

// 전역에서 호출 가능하도록 추가
global.NemoAr = NemoAr;